-base-color);\n        --button-padding: 0;\n        --button-hover-padding: 0;\n        --button-height: min-content;\n        --button-hover-height: min-content;\n\n        border: none;\n        text-transform: none;\n        margin: 0;\n        padding: 0;\n        font-weight: normal;\n        text-align: start;\n\n        &:active {\n            text-decoration: underline;\n            padding: 0;\n        }\n\n        @include hoverable {\n            &:hover,\n            &:focus {\n                text-decoration: underline;\n                padding: 0;\n            }\n        }\n    }\n}\n","/**\n * ScandiPWA - Progressive Web App for Magento\n *\n * Copyright © Scandiweb, Inc. All rights reserved.\n * See LICENSE for license details.\n *\n * @license OSL-3.0 (Open Software License (\"OSL\") v. 3.0)\n * @package scandipwa/scandipwa\n * @link https://github.com/scandipwa/scandipwa\n */\n\n%loader {\n    position: relative;\n    width: 40px;\n    height: 23px;\n    background-color: #fff;\n    margin: 12px 0;\n    border-inline-start: solid 5px var(--primary-base-color);\n    border-inline-end: solid 5px var(--primary-base-color);\n    box-sizing: border-box;\n    animation: rotate 2s linear infinite;\n    transform-origin: 50% 50%;\n\n    &::before,\n    &::after {\n        content: '';\n        position: absolute;\n        z-index: 1;\n        width: 28px;\n        height: 28px;\n        transform: scaleY(.5774) rotate(-45deg);\n        background-color: inherit;\n        inset-inline-start: 1px;\n        box-sizing: border-box;\n    }\n\n    // ltr + rtl adjusted, don't replace 'left' and 'right' here\n\n    &::before {\n        inset-block-start: -14px;\n        border-top: solid 7px var(--primary-base-color);\n        border-right: solid 7px var(--primary-base-color);\n    }\n\n    &::after {\n        inset-block-end: -14px;\n        border-bottom: solid 7px var(--primary-base-color);\n        border-left: solid 7px var(--primary-base-color);\n    }\n\n