ary-light-color: lighten($default-primary-base-color, 15%);\n$default-secondary-base-color: #eee;\n$default-secondary-dark-color: darken($default-secondary-base-color, 15%);\n$default-secondary-light-color: lighten($default-secondary-base-color, 15%);\n$font-muli: 'Muli', sans-serif;\n$font-standard-size: 62.5%;\n","/**\n * ScandiPWA - Progressive Web App for Magento\n *\n * Copyright © Scandiweb, Inc. All rights reserved.\n * See LICENSE for license details.\n *\n * @license OSL-3.0 (Open Software License (\"OSL\") v. 3.0)\n * @package scandipwa/scandipwa\n * @link https://github.com/scandipwa/scandipwa\n */\n\n@mixin desktop {\n    @media (min-width: 811px) {\n        @content;\n    }\n}\n\n@mixin ultra-narrow-desktop {\n    @media (min-width: 810px)\n        and (max-width: 1160px) {\n        @content;\n    }\n}\n\n@mixin narrow-desktop {\n    @media (min-width: 1024px)\n        and (max-width: 1280px) {\n        @content;\n    }\n}\n\n@mixin wide-desktop {\n    @media (min-width: 1280px) {\n        @content;\n    }\n}\n\n@mixin tablet {\n    @media (min-width: 811px)\n        and (max-width: 1024px)\n        and (-webkit-min-device-pixel-ratio: 1) {\n        @content;\n    }\n}\n\n@mixin tablet-portrait {\n    @media (min-width: 811px)\n        and (max-width: 1023px)\n        and (-webkit-min-device-pixel-ratio: 1)\n        and (orientation: portrait) {\n        @content;\n    }\n}\n\n@mixin tablet-landscape {\n    @media(min-width: 811px)\n        and (max-width: 1023px)\n        and (-webkit-min-device-pixel-ratio: 1)\n        and (orientation: landscape) {\n        @content;\n    }\n}\n\n@mixin mobile {\n    @media screen and (max-width: 810px) {\n        @content;\n    }\n}\n\n@mixin standalone {\n    /* stylelint-disable-next-line media-feature-name-no-unknown */\n    @media all and (display-mode: standalone) {\n        @content;\n    }\n}\n\n@mixin hoverable {\n    @media (hover: hover) and (pointer: fine) { \n        @content;\n    }\n}\n","/**\n * Scand