don't remove 'left'\n            border-block-start: var(--my-account-overlay-border-width) solid var(--overlay-desktop-border-color);\n            border-left: var(--my-account-overlay-border-width) solid var(--overlay-desktop-border-color);\n        }\n    }\n\n    button {\n        width: 100%;\n\n        &.Button_likeLink {\n            width: auto;\n        }\n    }\n\n    &-ForgotPassword {\n        width: auto;\n        margin-block-start: 8px;\n    }\n\n    input {\n        width: 100%;\n    }\n\n    &-Heading {\n        font-size: 18px;\n        font-weight: bold;\n        padding-block-start: 0;\n        margin-block-end: 0;\n\n        @include mobile {\n            padding-block-end: 14px;\n            padding-inline: 14px;\n        }\n    }\n\n    &-Notification {\n        background-color: var(--primary-dark-color);\n        padding: 24px;\n\n        @include mobile {\n            padding: 28px;\n        }\n    }\n\n    &-Navigation {\n        li {\n            margin: 0;\n            padding: 0;\n\n            &::before {\n                content: none;\n            }\n\n            &:last-child {\n                margin-block-end: 0;\n            }\n\n            a {\n                display: block;\n            }\n        }\n\n        a,\n        button {\n            width: 100%;\n            text-align: start;\n            padding: 12px 24px;\n            font-size: 14px;\n\n            @include mobile {\n                padding: 14px 28px;\n                font-size: 17px;\n            }\n\n            &:hover {\n                background-color: var(--color-cart-light);\n                text-decoration: none;\n            }\n\n            &:focus {\n                text-decoration: none;\n            }\n\n            @include desktop {\n                padding: 6px;\n            }\n        }\n\n        @include desktop {\n            padding: 6px;\n        }\n    }\n\n    &-Action form,\n    &-Additional_state_signIn {\n        @include mobile {\n            padding-block-start: 14px;\n            padding-inline: 14px;\n        }\n    }\n\n    &-SignInLink {\n        width: auto;\n        margin-block-start: 0;\n        line-height: 20px;\n    }\n\n    &-ResetPassword {\n        width: 100%;\n        margin-block-start: 24px;\n    }\n\n    &-Action {\n        display: grid;\n        grid-template-rows: max-content 1fr;\n\n        @include desktop {\n            overflow-y: auto;\n            max-height: calc(100vh - var(--header-nav-height) - var(--popup-bottom-offset));\n        }\n\n        &_state {\n            &_createAccount {\n                h4 {\n                    padding-inline-end: 12px;\n                    display: inline;\n\n                    @include mobile {\n                        padding-inline-end: 14px;\n                    }\n                }\n\n                h3 {\n                    margin-block-end: 12px;\n\n                    @include mobile {\n                        margin-block-end: 14px;\n                    }\n                }\n\n                .Field {\n                    margin-block-start: 8px;\n\n                    @include mobile {\n                        margin-block-start: 10px;\n                    }\n                }\n\n                @include mobile {\n                    padding-block-start: 14px;\n                }\n            }\n\n            &_forgotPassword {\n                h4 {\n                    width: initial;\n                }\n\n                button {\n                    font-weight: bold;\n                }\n            }\n\n            &_signIn {\n                h4 {\n                    text-align: center;\n                }\n\n                .Form .Button_likeLink {\n                    margin-block-start: 8px;\n                }\n            }\n\n            &_loggedIn {\n                .MyAccountOverlay-Heading {\n                    @include desktop {\n                        display: none;\n                    }\n                }\n\n                @include desktop {\n                    width: 180px;\n                    padding: 0;\n                }\n            }\n        }\n\n        h4 {\n            font-size: 14px;\n            font-weight: bold;\n            margin-block-end: 6px;\n\n            @include mobile {\n                font-size: 16px;\n                margin-block-end: 7px;\n            }\n        }\n\n        h3 {\n            margin-block-start: 0;\n        }\n\n        @include desktop {\n            padding-inline: 16px;\n            padding-block: 24px 16px;\n        }\n    }\n\n    &-Additional {\n        padding-block-start: 24px;\n\n        section {\n            display: flex;\n            padding-block-start: 20px;\n\n            &:first-of-type {\n                padding-block-start: 0;\n            }\n        }\n\n        &_state {\n            &_createAccount {\n                padding-block-start: 24px;\n\n                @include mobile {\n                    padding-block-start: 28px;\n                    display: none;\n                }\n\n                h4 {\n                    margin: 0;\n                    padding-inline-end: 0;\n                }\n\n                button {\n                    width: initial;\n                    margin-block: 0;\n                    margin-inline-start: 5px;\n\n                    &:hover,\n                    &:focus {\n                        margin-block: 0;\n                    }\n                }\n            }\n\n            &_signIn {\n                section {\n                    align-items: center;\n                }\n\n                button {\n                    margin-inline-start: 5px;\n                }\n            }\n\n            &_confirmEmail {\n                padding-block-start: 0;\n            }\n\n            &_forgotPassword {\n                section {\n                    display: flex;\n\n                    button {\n                        width: initial;\n                        margin-block-start: 0;\n                        padding-inline-start: 5px;\n                        font-weight: 600;\n\n                        &:hover,\n                        &:focus {\n                            padding-inline-start: 5px;\n                        }\n                    }\n\n                    h4 {\n                        margin-block-end: 0;\n                        align-self: center;\n                    }\n\n                    &:last-of-type {\n                        align-items: center;\n                        padding-block-start: 1px;\n                    }\n                }\n            }\n\n            &_forgotPasswordSuccess {\n                padding-block-start: 0;\n\n                h4 {\n                    font-size: 12px;\n                    line-height: 1.5;\n                    padding-block-end: 17px;\n                    font-weight: normal;\n                    letter-spacing: normal;\n\n                    @include mobile {\n                        font-size: 14px;\n                        padding-block-end: 20px;\n                    }\n                }\n            }\n        }\n    }\n\n    &-CloseButton {\n        &::before,\n        &::after {\n            background-color: var(--header-color);\n            content: '';\n            height: 20px;\n            position: absolute;\n            inset-inline-end: 27px;\n            inset-block-start: -13px;\n            transform-origin: 50% 50%;\n            width: 1px;\n            z-index: 1;\n        }\n\n        &::before {\n            transform: rotate(-45deg);\n        }\n\n        &::after {\n            transform: rotate(45deg);\n        }\n    }\n\n    &-ForgotPasswordButton {\n        @include desktop {\n            display: none;\n        }\n    }\n\n    &-Buttons {\n        margin-block-start: 6px;\n\n        @include mobile {\n            margin-block-start: 7px;\n        }\n    }\n\n    &-Button {\n        &_likeLink {\n            font-size: 12px;\n            margin-block-start: 14px;\n\n            @include mobile {\n                font-size: 13px;\n                margin-block-start: 17px;\n            }\n        }\n    }\n\n    &-Checkbox {\n        margin-block: 11px 4px;\n    }\n}\n","/**\n * ScandiPWA - Progressive Web App for Magento\n *\n * Copyright © Scandiweb, Inc. All rights reserved.\n * See LICENSE for license details.\n *\n * @license OSL-3.0 (Open Software License (\"OSL\") v. 3.0)\n * @package scandipwa/scandipwa\n * @link https://github.com/scandipwa/scandipwa\n */\n\n$white: #fff;\n$black: #0a0903;\n$default-primary-base-color: #f26323;\n$default-primary-dark-color: darken($default-primary-base-color, 15%);\n$default-primary-light-color: lighten($default-primary-base-color, 15%);\n$default-secondary-base-color: #eee;\n$default-secondary-dark-color: darken($default-secondary-base-color, 15%);\n$default-secondary-light-color: lighten($default-secondary-base-color, 15%);\n$font-muli: 'Muli', sans-serif;\n$font-standard-size: 62.5%;\n","/**\n * ScandiPWA - Progressive Web App for Magento\n *\n * Copyright © Scandiweb, Inc. All rights reserved.\n * See LICENSE for license details.\n *\n * @license OSL-3.0 (Open Software License (\"OSL\") v. 3.0)\n * @package scandipwa/scandipwa\n * @link https://github.com/scandipwa/scandipwa\n */\n\n@mixin desktop {\n    @media (min-width: 811px) {\n        @content;\n    }\n}\n\n@mixin ultra-narrow-desktop {\n    @media (min-width: 810px)\n        and (max-width: 1160px) {\n        @content;\n    }\n}\n\n@mixin narrow-desktop {\n    @media (min-width: 1024px)\n        and (max-width: 1280px) {\n        @content;\n    }\n}\n\n@mixin wide-desktop {\n    @media (min-width: 1280px) {\n        @content;\n    }\n}\n\n@mixin tablet {\n    @media (min-width: 811px)\n        and (max-width: 1024px)\n        and (-webkit-min-device-pixel-ratio: 1) {\n        @content;\n    }\n}\n\n@mixin tablet-portrait {\n    @media (min-width: 811px)\n        and (max-width: 1023px)\n        and (-webkit-min-device-pixel-ratio: 1)\n        and (orientation: portrait) {\n        @content;\n    }\n}\n\n@mixin tablet-landscape {\n    @media(min-width: 811px)\n        and (max-width: 1023px)\n        and (-webkit-min-device-pixel-ratio: 1)\n        and (orientation: landscape) {\n        @content;\n    }\n}\n\n@mixin mobile {\n    @media screen and (max-width: 810px) {\n        @content;\n    }\n}\n\n@mixin standalone {\n    /* stylelint-disable-next-line media-feature-name-no-unknown */\n    @media all and (display-mode: standalone) {\n        @content;\n    }\n}\n\n@mixin hoverable {\n    @media (hover: hover) and (pointer: fine) { \n        @content;\n    }\n}\n","/**\n * ScandiPWA - Progressive Web App for Magento\n *\n * Copyright © Scandiweb, Inc. All rights reserved.\n * See LICENSE for license details.\n *\n * @license OSL-3.0 (Open Software License (\"OSL\") v. 3.0)\n * @package scandipwa/scandipwa\n * @link https://github.com/scandipwa/scandipwa\n */\n@mixin button-disabled {\n    pointer-events: none;\n    opacity: .25;\n}\n\n@mixin button {\n    align-items: center;\n    background-color: var(--button-background);\n    border-radius: var(--button-border-radius);\n    cursor: pointer;\n    display: inline-flex;\n    color: var(--button-color);\n    font-size: 14px;\n    font-weight: 700;\n    justify-content: center;\n    height: var(--button-height);\n    line-height: normal;\n    padding-inline: var(--button-padding);\n    text-decoration: var(--button-text-decoration);\n    text-transform: uppercase;\n    transition-duration: .25s;\n    transition-timing-function: ease-out;\n    transition-property: background-color, color, border;\n    will-change: background-color, color, border;\n\n    @include mobile {\n        user-select: none;\n        width: 100%;\n    }\n\n    border: {\n        color: var(--button-border);\n        style: solid;\n        width: var(--button-border-width);\n    }\n\n    &:hover,\n    &:focus {\n        text-decoration: var(--button-hover-text-decoration);\n    }\n\n    &:not([disabled]) {\n        &:hover {\n            @include desktop {\n                background-color: var(--button-hover-background);\n                border-color: var(--button-hover-border);\n                border-radius: var(--button-hover-border-radius);\n                color: var(--button-hover-color);\n                height: var(--button-hover-height);\n                padding-inline: var(--button-hover-padding);\n            }\n        }\n    }\n\n    &[disabled] {\n        @include button-disabled();\n    }\n\n    &_isHollow {\n        background-color: var(--hollow-button-background);\n        border-color: var(--hollow-button-border);\n        border-radius: var(--hollow-button-border-radius);\n        color: var(--hollow-button-color);\n        height: var(--hollow-button-height);\n        padding-inline: var(--hollow-button-padding);\n        text-decoration: var(--hollow-button-text-decoration);\n\n        &:not([disabled]) {\n            &:hover {\n                @include desktop {\n                    background-color: var(--hollow-button-hover-background);\n                    border-color: var(--hollow-button-hover-border);\n                    border-radius: var(--hollow-button-hover-border-radius);\n                    color: var(--hollow-button-hover-color);\n                    height: var(--hollow-button-hover-height);\n                    padding-inline: var(--hollow-button-hover-padding);\n                    text-decoration: var(--hollow-button-hover-text-decoration);\n                }\n            }\n        }\n    }\n\n    &_isWithoutBorder {\n        border: 0;\n    }\n\n    &_likeLink {\n        --button-border: transparent;\n        --button-background: transparent;\n        --button-color: var(--primary-base-color);\n        --button-hover-border: transparent;\n        --button-hover-background: transparent;\n        --button-hover-color: var(--primary-base-color);\n        --button-padding: 0;\n        --button-hover-padding: 0;\n        --button-height: min-content;\n        --button-hover-height: min-content;\n\n        border: none;\n        text-transform: none;\n        margin: 0;\n        padding: 0;\n        font-weight: normal;\n        text-align: start;\n\n        &:active {\n            text-decoration: underline;\n            padding: 0;\n        }\n\n        @include hoverable {\n            &:hover,\n            &:focus {\n                text-decoration: underline;\n                padding: 0;\n            }\n        }\n    }\n}\n","/**\n * ScandiPWA - Progressive Web App for Magento\n *\n * Copyright © Scandiweb, Inc. All rights reserved.\n * See LICENSE for license details.\n *\n * @license OSL-3.0 (Open Software License (\"OSL\") v. 3.0)\n * @package scandipwa/scandipwa\n * @link https://github.com/scandipwa/scandipwa\n */\n\n%loader {\n    position: relative;\n    width: 40px;\n    height: 23px;\n    background-color: #fff;\n    margin: 12px 0;\n    border-inline-start: solid 5px var(--primary-base-color);\n    border-inline-end: solid 5px var(--primary-base-color);\n    box-sizing: border-box;\n    animation: rotate 2s linear infinite;\n    transform-origin: 50% 50%;\n\n    &::before,\n    &::after {\n        content: '';\n        position: absolute;\n        z-index: 1;\n        width: 28px;\n        height: 28px;\n        transform: scaleY(.5774) rotate(-45deg);\n        background-color: inherit;\n        inset-inline-start: 1px;\n        box-sizing: border-box;\n    }\n\n    // ltr + rtl adjusted, don't replace 'left' and 'right' here\n\n    &::before {\n        inset-block-start: -14px;\n        border-top: solid 7px var(--primary-base-color);\n        border-right: solid 7px var(--primary-base-color);\n    }\n\n    &::after {\n        inset-block-end: -14px;\n        border-bottom: solid 7px var(--primary-base-color);\n        border-left: solid 7px var(--primary-base-color);\n    }\n\n    span {\n        position: absolute;\n        inset-block: 0;\n        inset-inline-start: 0;\n        inset-inline-end: 0;\n        margin: auto;\n        width: 18px;\n        height: 18px;\n        background-color: rgb(80, 88, 101);\n        border-radius: 50%;\n        z-index: 2;\n    }\n}\n","/**\n * ScandiPWA - Progressive Web App for Magento\n *\n * Copyright © Scandiweb, Inc. All rights reserved.\n * See LICENSE for license details.\n *\n * @license OSL-3.0 (Open Software License (\"OSL\") v. 3.0)\n * @package scandipwa/scandipwa\n * @link https://github.com/scandipwa/scandipwa\n */\n\n:root {\n    --star-size: 18px;\n    --plus-minus-icon-size: 24px;\n    --wishlist-heart-size: 23px;\n}\n\n@mixin icon-black-hover-effect {\n    fill: var(--color-black);\n\n    &:hover {\n        fill: var(--primary-base-color);\n    }\n}\n\n@mixin icon-primary {\n    fill: var(--primary-base-color);\n\n    @include hoverable { \n        &:hover {\n            fill: var(--primary-dark-color);\n        }\n    }\n\n    &:active {\n        fill: var(--primary-dark-color);\n    }\n}\n\n@mixin not-available {\n    content: '';\n    position: absolute;\n    width: 1px;\n    height: 32px;\n    transform-origin: 50% 50%;\n    inset-inline-start: 15px;\n    inset-block-start: 1px;\n    transform: rotate(45deg);\n    box-shadow: none;\n    border-radius: 0;\n    opacity: 1;\n}\n\n@mixin ios-share {\n    $color-dodger-blue: #3b9cfe;\n\n    border: 2px solid $color-dodger-blue;\n    inset-block-end: -3px;\n    height: 18px;\n    margin: 0 4px;\n    width: 18px;\n\n    &::before {\n        content: '';\n        border-inline-start: 2px solid $color-dodger-blue;\n        border-block-start: 2px solid $color-dodger-blue;\n        display: block;\n        height: 6px;\n        inset-inline-start: 3px;\n        position: relative;\n        inset-block-start: -9px;\n        transform: rotate(45deg);\n        width: 6px;\n\n        [dir=\"rtl\"] & {\n            transform: rotate(315deg);\n        }\n    }\n\n    &::after {\n        border-inline-start: 2px solid $color-dodger-blue;\n        content: '';\n        display: block;\n        height: 16px;\n        inset-inline-start: 6px;\n        position: relative;\n        inset-block-start: -16px;\n        width: 19px;\n    }\n}\n\n@mixin ios-plus {\n    $color-storm-grey: #7b7b86;\n\n    background: linear-gradient($white, $white),\n        linear-gradient($white, $white),\n        $color-storm-grey;\n    background-position: center;\n    background-repeat: no-repeat;\n    background-size: 52% 1px, 1px 48%;\n    border-radius: 5px;\n    height: 20px;\n    margin: 0 4px;\n    width: 20px;\n}\n\n@mixin rtl-icon {\n    [dir=\"rtl\"] & {\n        transform: scaleX(-1);\n    }\n}\n"],"sourceRoot":""}]);
